import React, { Component } from 'react';
export default class Header extends Component {
  render() {
	  let resumeData = this.props.resumeData;
    return (
      <React.Fragment>
      <header id="home">
  <nav id="nav-wrap">
    <a className="mobile-btn" href="#nav-wrap" title="Show navigation">Show navigation</a>
    <a className="mobile-btn" href="#" title="Hide navigation">Hide navigation</a>
    <ul id="nav" className="nav">
      <li className="current"><a className="smoothscroll" href="#home">Accueil</a></li>
      <li><a className="smoothscroll" href="#about">A propos</a></li>
	  <li><a className="smoothscroll" href="#quiz">Quizz</a></li>
      <li><a className="smoothscroll" href="#resume">CV</a></li>
      <li><a className="smoothscroll" href="#portfolio">Projets</a></li>
      <li><a className="smoothscroll" href="#testimonials">Témoignages</a></li>
      <li><a className="smoothscroll" href="#contact">Contact</a></li>
    </ul> {/* end #nav */}
  </nav> {/* end #nav-wrap */}
  <div className="row banner">
    <div className="banner-text">
      <h1 className="responsive-headline">Je m'apelle {resumeData.name}.</h1>
      <h3>Je suis {resumeData.role}. {resumeData.roleDescription}</h3>
      <hr />
      <ul className="social">
        <li><a href="#"><i className="fa fa-facebook" /></a></li>
        <li><a href="#"><i className="fa fa-envelope" /></a></li>
        <li><a href="#"><i className="fa fa-linkedin" /></a></li>
        <li><a href="#"><i className="fa fa-skype" /></a></li>
      </ul>
    </div>
  </div>
  <p className="scrolldown">
    <a className="smoothscroll" href="#about"><i className="icon-down-circle" /></a>
  </p>
</header>
      </React.Fragment>
    );
  }
}